import React from "react";
import s from './FormsControls.module.css'
import {Field} from "redux-form";

export const Input = ({input, meta, ...props}) => {
    const hasError = meta.touched && meta.error;
    return (
        <div className={s.wrapper + ' ' + (hasError ? s.error : '')}>
            <input {...input} {...props} className={s.inputs__item}/>
            {hasError && <span>Field is required</span>}
        </div>
    )
};

export const InputProfile = ({input, ...props}) => {
    return (
        <input {...input} {...props} className={s.inputsProfile}/>

    )
};

export const InputPost = ({input, meta, ...props}) => {
    return (
        <input className={s.addNewPost}
               type="text"
               placeholder='This is my new post...'{...input} {...props}/>
    )
};

export const createField = (placeholder, name, validators, component, props = {}, text = '') => {
    return (
        <div>
            <Field placeholder={placeholder}
                   name={name}
                   validate={validators}
                   component={component}
                   {...props}/> {text}
        </div>
    )
};