import React from 'react';
import s from "./Users.module.css";
import userAvatar from "../../Assets/images/avatar.png";
import {NavLink} from "react-router-dom";

const UserItem = ({user, followingInProgress, unfollow, follow}) => {

    return (
        <div>
            <div className={s.usersList} id={user.id} key={user.id}>
                <NavLink to={'/Profile/' + user.id}>
                    <img src={user.photos.small != null ? user.photos.small : userAvatar}
                         alt="avatar"
                         className={s.ava}
                    />
                </NavLink>
                <div className={s.wrapper}>
                    <div className={s.info}>
                        <div className={s.name}>{user.name}</div>
                    </div>
                    <div>
                        {user.followed
                            ? <button disabled={followingInProgress.some(id => id === user.id)}
                                      className={`${s.btn} ${s.btnUfoll}`}
                                      onClick={() => {
                                          unfollow(user.id)
                                      }}>Unfollow</button>
                            : <button disabled={followingInProgress.some(id => id === user.id)}
                                      className={`${s.btn} ${s.btnFoll}`}
                                      onClick={() => {
                                          follow(user.id)
                                      }}>Follow</button>
                        }
                    </div>
                </div>
            </div>
        </div>
    )
};

export default UserItem;