import React from 'react';
import Post from "./Post";
import  s from './MyPosts.module.css';
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator} from "../../../utils/validators/validators";
import {InputPost} from "../../common/FormsControls/FormsControl";

const maxLength60 = maxLengthCreator(60);

const MyPosts = React.memo( (props) => {

    let postsElements = props.posts.map(p =>
            <Post id={p.id} message={p.message} like={p.like} key={p.id}/>
        );


    const AddPost = (value) => {
            props.addPost(value.postText);
    };

    return (
        <div className={s.wall}>
            <h2 className={s.postsHeader}>My Posts</h2>
            <div className={s.newPost}>
                <PostsReduxForm onSubmit={AddPost}/>
            </div>
            { postsElements }
        </div>
    );
});

const PostForm = (props) => {
  return (
      <form onSubmit={props.handleSubmit} className={s.form__wrapper}>
          <Field name='postText'
                 component={InputPost}
                 validate={[maxLength60]}
          />
          <button className={s.btn}>Add post</button>
      </form>
  )
};

const PostsReduxForm = reduxForm ({
    form : 'posts'
})(PostForm);

export default MyPosts;