import React from 'react';
import s from "../ProfileInfo.module.css";


const ProfileLinks = ({contacts}) => {
    const items = Object.keys(contacts).map(key => {
         if (contacts[key] === null) {
            return null
        } else {
        return <a href={contacts[key]} key={key} className={s.items} target="_blank">
           {key}
        </a>}

    });
    return (
        <div className={s.links}>

            <p>ontacts :</p>
            {items}
            {/*<a href={contacts.facebook} className={s.items}>*/}
            {/*    <i className="fa fa-facebook-square"></i>*/}
            {/*</a>*/}
            {/*<a href={contacts.vk} className={s.items}>*/}
            {/*    <i className="fa fa-vk"></i>*/}
            {/*</a>*/}
            {/*<a href={contacts.twitter} className={`${s.items} ${s.twit}`}>*/}
            {/*    <i className="fa fa-twitter-square"></i>*/}
            {/*</a>*/}
            {/*<a href={contacts.instagram} className={`${s.items} ${s.inst}`}>*/}
            {/*    <i className="fa fa-instagram"></i>*/}
            {/*</a>*/}
            {/*<a href={contacts.github} className={`${s.items} ${s.git}`}>*/}
            {/*    <i className="fa fa-github-square"></i>*/}
            {/*</a>*/}
        </div>
    )
};

export default ProfileLinks;