import {usersAPI} from "../api/api";
import {updateObjectInArray} from "../utils/object-helpers";

             // ACTIONS

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_USERS_TOTAL_COUNT = 'SET_USERS_TOTAL_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';

let initialState = {
    usersData : [],
    pageSize : 8,
    totalCount : 1,
    currentPage : 1,
    isFetching : false,
    followingInProgress : []
};

                // REDUCER

const userReducer = (state = initialState, action) => {

    switch (action.type) {

        case FOLLOW :

            return  {
                ...state,
                usersData : updateObjectInArray(state.usersData, action.userId,'id', {followed : true})
            };

        case UNFOLLOW :
            return {
                ...state,
                usersData : updateObjectInArray(state.usersData, action.userId,'id', {followed : false})
            };

        case SET_USERS : {
            return {
              ...state, usersData : action.usersData
            }
        }

        case SET_CURRENT_PAGE : {
            return {
                ...state,
                currentPage : action.currentPage
            }
        }

        case SET_USERS_TOTAL_COUNT : {
            return {
                ...state,
                totalCount : action.totalCount
            }
        }

        case TOGGLE_IS_FETCHING : {
            return {
                ...state,
                isFetching: action.isFetching
            }
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS : {
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [state.followingInProgress, action.id]
                    : state.followingInProgress.filter(id => id !== action.id)
            }
        }
        default :
            return state;

    }
};

                // ACTION CREATORS

export const followSuccess = (userId) => ({type : FOLLOW, userId});
export const unfollowSuccess = (userId) => ({type : UNFOLLOW, userId});
export const setUsers = (usersData) => ({type : SET_USERS, usersData});
export const setCurrentPage = (currentPage) => ({type : SET_CURRENT_PAGE, currentPage});
export const setUsersTotalCount = (totalCount) => ({type : SET_USERS_TOTAL_COUNT, totalCount});
export const toggleIsFetching = (isFetching) => ({type : TOGGLE_IS_FETCHING, isFetching});
export const toggleFollowingProgress = (isFetching, id) => ({type : TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, id});

                // THUNK

export const getUsers = (pageSize, currentPage) => async (dispatch) => {
        dispatch(toggleIsFetching(true));
        dispatch(setCurrentPage(currentPage));
        let data = await usersAPI.getUsers(pageSize, currentPage);
            dispatch(toggleIsFetching(false));
            dispatch(setUsers(data.items));
            dispatch(setUsersTotalCount(data.totalCount));
};

const followUnfollowFlow = async (dispatch, id, apiMethod, actionCreator) => {
    let data = await apiMethod(id);
    if (data.resultCode === 0) {
        dispatch(actionCreator(id));
    }
    dispatch(toggleFollowingProgress(false, id));
};

export const follow = (id) => (dispatch) => {
    followUnfollowFlow(dispatch, id, usersAPI.followUsers.bind(usersAPI), followSuccess);
};

export const unfollow = (id) => (dispatch) => {
    followUnfollowFlow(dispatch, id, usersAPI.unfollowUsers.bind(usersAPI), unfollowSuccess);
};


export default userReducer;