import React from 'react';
import s from "../ProfileInfo.module.css";
import ProfileStatusWithHooks from "../ProfileStatus/ProfileStatusWithHooks";
import ProfileLinks from "../ProfileLinks/ProfileLinks";

const ProfileData = (props) => {
    return (
        <div className={s.wrapper}>
            {props.isOwner && <div><button className={s.editProfile} onClick={props.goToEditMode}>Edit profile</button></div>}
            <h3 className={s.profileHeader}>{props.profile.fullName}</h3>
            <ProfileStatusWithHooks status={props.status}
                                    updateStatus={props.updateStatus}
                                    isOwner={props.isOwner}
            />
            <div>
                <h4 className={s.about}>About me :</h4>
                <p>{props.profile.aboutMe}</p>
            </div>
            {props.profile.lookingForAJob
                ? <div className={s.workT}>Looking for a job</div>
                : <div className={s.workF}>Already working</div>
            }
            <div className={s.wrapper_description}>
                <h4 className={s.description}>{props.profile.lookingForAJobDescription || 'No description'}</h4>
            </div>
            <ProfileLinks {...props.profile}/>
        </div>
    )
};

export default ProfileData;