import React from 'react';
import s from './Friends.module.css';
import FriendsElement from './FriendsElement';
import FriendsBar from "./FriendsBar/FriendsBar";


const Friends = (props) => {

    let friendsElements = props.friendsList.map(f => <FriendsElement name={f.name}
                                                                     id={f.id}
                                                                     key={f.id}
                                                                     followed={f.followed}
                                                                     photos={f.photos.small}

    />);

    return (

        <div className={s.friendsPage}>
            <FriendsBar />
            {friendsElements}
        </div>

    );
};


export default Friends;