import React from 'react';
import s from './ProfileDataForm.module.css';
import {Form, reduxForm} from "redux-form";
import {createField,InputProfile} from "../../../common/FormsControls/FormsControl";


const ProfileDataForm = (props) => {
    return (
        <Form onSubmit={props.handleSubmit}>
            <div className={s.btnWrapper}>
                <button className={s.btn}>Save changes</button>
            </div>
            <div className={s.wrapperName}>
                <b>Full name:</b>
                {createField('fullName', 'fullName', [], InputProfile)}
            </div>
            <div className={s.wrapperName }>
                <b>Looking For A Job :</b>
                <div className={s.check}>
                    {createField('', 'lookingForAJob', [], 'input', {type: 'checkbox'})}
                </div>
            </div>
            <div>
                <b>lookingForAJobDescription : </b>
                <div className={s.wrapperAll}>
                    {createField('lookingForAJobDescription me', 'lookingForAJobDescription', [], InputProfile)}
                </div>
            </div>
            <div>
                <b>About me : </b>
                <div className={s.wrapperAll}>
                    {createField('About me', 'aboutMe', [], InputProfile)}
                </div>
            </div>
            {Object.keys(props.contacts).map(key => {
                return <div>
                    <b>{key} : </b>
                    <div className={s.wrapperAll}>
                        {createField(key, 'contacts.' + key, [], InputProfile)}
                    </div>
                </div>
        })}

        </Form>
    )
};

const ProfileDataReduxForm = reduxForm({form: 'edit-profile'})(ProfileDataForm);

export default ProfileDataReduxForm;