import React, {useState} from 'react';
import s from "./ProfileInfo.module.css";
import Preloader from "../../common/Preloader/Preloader";
import userAvatar from "../../../Assets/images/avatar.png";
import ProfileData from "./ProfileData/ProfileData";
import ProfileDataForm from "./ProfileDataForm/ProfileDataForm";


const ProfileInfo = (props) => {

    let [editMode, setEditMode] = useState(false);

    if (!props.profile) {
        return <Preloader/>
    }

    const onMainPhotoChange = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0]);
        }
    };

    const onSubmit = (formData) => {
      props.saveProfile(formData);
        setEditMode(false);
    };

    return (
        <div>
            <div className={s.info}>
                <div className={s.infoImage}>
                    <img src={props.profile.photos.large != null ? props.profile.photos.large : userAvatar} alt=""/>
                    {props.isOwner && <input type={'file'} onChange={onMainPhotoChange}/>}
                </div>
                <div className={s.profileInfo}>
                    {editMode
                        ? <ProfileDataForm initialValues={props.profile} {...props.profile} onSubmit={onSubmit}/>
                        : <ProfileData goToEditMode={() => {setEditMode(true)}} {...props}/>
                    }
                </div>
            </div>
        </div>
    );
};


export default ProfileInfo;

