import React, {useState} from 'react';
import s from './Paginator.module.css';

const Paginator = ({totalCount, pageSize, currentPage, onPageChanged, portionSize = 5}) => {

    let pagesCount = Math.ceil(totalCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount/ portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;

    return (
        <div className={s.paginator}>
            {portionNumber > 2 &&
        <button className={s.btn} onClick={() => setPortionNumber(portionNumber = 1)}>Start</button>}
            {portionNumber > 1 &&
            <button className={s.btn} onClick={() => setPortionNumber(portionNumber - 1)}>Prev</button>}
            {pages
                    .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                    .map(p => {
                        return <div>
                            <span className={currentPage === p && s.selectedPage}
                                  onClick={() => {
                                      onPageChanged(p)
                                  }}>
                        {p}
                        </span>
                        </div>
                })
            }
            {portionCount > portionNumber &&
            <button className={s.btn} onClick={() => setPortionNumber(portionNumber + 1)}>Next</button>}
            {portionCount > portionNumber + 1 &&
            <button className={s.btn} onClick={() => setPortionNumber(portionNumber = portionCount)}>End</button>}
        </div>
    )
};

export default Paginator;