import React from 'react';
import s from './FriendsElement.module.css';
import {NavLink} from "react-router-dom";
import userAvatar from "../../../Assets/images/avatar.png";

const FriendsElement = ({followed,id,photos, ...props}) => {
    return (
        followed
            ? <div className={s.friendsItem}>
                <div className={s.img}> <NavLink to={'/Profile/' + id}>
                    <img src={photos != null ? photos : userAvatar}
                         alt="avatar"
                         className={s.ava}
                    />
                </NavLink>
                </div>
                <div className={s.textBlock}>
                    <div className={s.name} id={props.id}>{props.name}</div>
                    <NavLink to="/Dialogs">Write message...</NavLink>
                </div>
              </div>
            : null

)
    ;
};

export default FriendsElement;