const ADD_NEW_MESSAGE = 'ADD-NEW-MESSAGE';

let initialState = {
    messagesData : [
        {id:1, message:'Hi'},
        {id:3, message:'Yop'},
        {id:4, message:'kek'},
        {id:6, message:'pipi'},
        {id:7, message:'piupiu'}
    ],
    dialogsData : [
        {id:1, name:'Anna'},
        {id:2, name:'Boris'},
        {id:3, name:'Dasha'},
        {id:4, name:'Robert'},
        {id:5, name:'Sasha'},
        {id:6, name:'Sveta'},
        {id:7, name:'Valera'}
    ],

};

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {

        case ADD_NEW_MESSAGE:
             return  {
                 ...state,
                 messagesData : [...state.messagesData, {id: 8, message: action.message}],

             };

        default : return state;
    }
};


export const sendMessage = (message) => ({ type : ADD_NEW_MESSAGE, message });
export default dialogsReducer;