import * as axios from "axios";

const instance = axios.create({
    baseURL : 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers : {
        'API-KEY' : '209e1589-c8e1-4114-9e39-f42219060211'
    }

});

export const usersAPI = {
    getUsers(pageSize = 20, currentPage = 1) {
        return  instance.get(`users?count=${pageSize}&&page=${currentPage}`).then(response => response.data)
    },
   followUsers(id)  {
        return instance.post(`follow/${id}`).then(response => response.data)
   },
    unfollowUsers(id) {
        return instance.delete(`follow/${id}`).then(response => response.data)
    },

};

export const profileAPi = {
    getProfile(userId) {
        return instance.get(`profile/${userId}`).then(response => response.data)
    },
    getStatus(userId) {
        return instance.get(`profile/status/${userId}`).then(response => response.data);
    },
    updateStatus(status) {
        return instance.put(`profile/status`, {status: status}).then(response => response.data);
    },
    savePhoto(photoFile) {
        const formData = new FormData();
        formData.append('image', photoFile);
        return instance.put(`profile/photo`, formData, {
            headers : {
                'Content-Type' : 'multipart/form-data'
            }
        }).then(response => response.data);
    },
    saveProfile(profile) {
        return instance.put(`profile`, profile).then(response => response.data);
    }

};

export const authAPI = {
    authMe() {
        return instance.get('auth/me').then(response => response.data);
    },
    login(email, password, rememberMe = false) {
        return instance.post('auth/login', {email, password, rememberMe}).then(response => response.data);
    },
    logout() {
        return instance.delete('auth/login').then(response => response.data);
    }
};

export const friendsAPI = {
    getUsers() {
        return  instance.get(`users?count=100`).then(response => response.data)
    }
};



