import React from 'react';
import s from './Nav-menu.module.css';
import FriendsList from "./FriendsList";
import {NavLink} from "react-router-dom";

const NavMenu = (props) => {

    let friendsElement = props.friendsList.map(f => <FriendsList name={f.name} key={f.id} id={f.id} />);
    
    return (
        <nav className={s.menu}>
            <ul className={s.listItem}>
              <li className={s.item}>
                  <NavLink to="/Profile" className={s.text} activeClassName={s.active}>Profile</NavLink>
              </li>
              <li className={s.item}>
                  <NavLink to="/Dialogs" className={s.text} activeClassName={s.active}>Messages</NavLink>
              </li>
              <li className={s.item}>
                  <NavLink to="/News" className={s.text} activeClassName={s.active}>News</NavLink>
              </li>
              <li className={s.item}>
                  <NavLink to="/Music" className={s.text} activeClassName={s.active}>Music</NavLink>
              </li>
              <li className={s.item}>
                  <NavLink to="/Settings" className={s.text} activeClassName={s.active}>Settings</NavLink>
              </li>
                <li className={s.item}>
                    <NavLink to="/Friends" className={`${s.text} ${s.textMod}`} activeClassName={s.active}>Friends</NavLink>
                    <div className={s.friendsList}>
                    { friendsElement }
                    </div>
                </li>
            </ul>
          </nav>
    );
};

export default NavMenu;