import {connect} from "react-redux";
import Friends from "./Friends";
import {withAuthRedirect} from "../hoc/withAuthRedirect";
import {compose} from "redux";
import React from "react";
import {addFriends} from "../../redux/friends-reducer";

export class FriendsContainer extends React.Component {

    componentDidMount() {
        this.props.addFriends();
    }

    render () {
        return <Friends {...this.props}/>
    }
}

let mapStateToProps = (state) => {
    return {
        friendsList : state.friendsPage.friendsList
    };
};


export default compose(withAuthRedirect,connect(mapStateToProps, {addFriends}))(FriendsContainer)