import React from 'react';
import s from './Dialogs.module.css';
import DialogItem from "./DialogItem";
import Messages from "./Message";
import {Field, reduxForm} from "redux-form";

const Dialogs = (props) => {

    let state = props.dialogsPage;

    let dialogsElements = state.dialogsData.map(d => <DialogItem id={d.id} name={d.name} key={d.id}/>);
    let messagesElement = state.messagesData.map(m => <Messages id={m.id} message={m.message} key={m.id}/>);

    const addNewMessage = (values) => {
        props.sendMessage(values.message);
    };

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsList}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                <div className={s.messageList}>
                    {messagesElement}
                </div>
                <MessageReduxForm state={state}
                                  onSubmit={addNewMessage}
                />
            </div>
        </div>
    );
};

const MessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div className={s.text}>
                <Field name='message'
                       component='input'
                       type="text"
                       placeholder='Type your message...'
                />
            </div>
            <button className={s.btnMessage}>Send</button>
        </form>
    )
};

const MessageReduxForm = reduxForm({
    form: 'message'
})(MessageForm);

export default Dialogs;