import React from 'react';
import s from './Post.module.css';

const Post = (props) => {

    return (
        <div className={s.item}>
            <p className={s.text}>{props.message}</p>
            <div className={s.posts}>
                <button className={s.btnLike}><i className="fa fa-heart"></i> </button>
                <span className={s.likes}>{props.like}</span>
                <button className={s.btnComment}><i className="fa fa-comments"></i></button>
            </div>
        </div>
    );
};

export default Post;